<template lang="">
    <div id="container">
        <h2>Food List</h2>
        <div class="in_wrap">
            <div class="content" v-for="(f,i) in foods" :key="i">
                <h3 @click="modalOpenTest(i)">{{f.title}}</h3>
            </div>
        </div>
    </div>
</template>
<script>
import test from '../assets/test.js'
export default {
    components : {

    },
    data(){
        return{
            foods : test,
            modal : false,
            crt : 0
        }
    },
    methods :{
        modalOpenTest(i){
            this.crt=i
            this.modal = true
        }
    }
}
</script>
